import http from "../http-common";

class DeptDataService {
    // Î™®Îì† Î∂ÄÏÑúÏ†ïÎ≥¥ Ï°∞Ìöå ÏöîÏ≤≠ Ìï®Ïàò + Î∂ÄÏÑúÎ™Ö Í≤ÄÏÉâ ÏöîÏ≤≠ Ìï®Ïàò 
    // dname : Î∂ÄÏÑúÎ™Ö 
    // page : ÌòÑÏû¨ ÌéòÏù¥ÏßÄ Î≤àÌò∏ 
    // size : Ìïú ÌéòÏù¥ÏßÄÎãπ ÌôîÎ©¥Ïóê ÌëúÏãúÌï† Îç∞Ïù¥ÌÑ∞ Í∞ØÏàò
    getAll(dname, page, size) {
    //   alert("ÌôïÏù∏Ïö©2")

        // get Î∞©Ïãù ÌÜµÏã† ÏöîÏ≤≠ -> @GetMapping("/api/dept")
        // 1) Ï†ÑÏ≤¥ Ï°∞Ìöå ÏôÄ
        // 2) Î∂ÄÏÑúÎ™Ö Ï°∞ÌöåÎ•º Í∞ôÏù¥ ÌïòÎäî Ìï®Ïàò getAll
        return http.get(`/dept?dname=${dname}&page=${page}&size=${size}`); 
    }

    // Î∂ÄÏÑúÎ≤àÌò∏Î°ú Ï°∞Ìöå ÏöîÏ≤≠ Ìï®Ïàò
    // get Î∞©Ïãù ÌÜµÏã† ÏöîÏ≤≠ -> @GetMapping("/api/dept/{dno}"), @PathVariable
    get(dno) {
        return http.get(`/dept/${dno}`)
    }

    // Î∂ÄÏÑúÏ†ïÎ≥¥ ÏÉùÏÑ±(insert) ÏöîÏ≤≠ Ìï®Ïàò
    // post Î∞©Ïãù ÌÜµÏã† ÏöîÏ≤≠ -> @PostMapping("/api/dept"), @RequestBody
    create(data) {
        console.log(data);
        return http.post("/dept", data);
    }

    // Î∂ÄÏÑúÏ†ïÎ≥¥ ÏàòÏ†ï(update) ÏöîÏ≤≠ Ìï®Ïàò
    // put Î∞©Ïãù ÌÜµÏã† ÏöîÏ≤≠ -> @PutMapping("/api/dept/{dno}"), @RequestBody
    update(dno, data) {
        return http.put(`/dept/${dno}`, data);
    }

    // Î∂ÄÏÑúÏ†ïÎ≥¥ ÏÇ≠Ï†ú(delete) ÏöîÏ≤≠ Ìï®Ïàò
    // delete Î∞©Ïãù ÌÜµÏã† ÏöîÏ≤≠ -> @DeleteMapping("/api/dept/deletion/{dno}")
    //                        , @PathVariable  
    delete(dno) {
        return http.delete(`/dept/deletion/${dno}`);
    }

    // Î∂ÄÏÑúÏ†ïÎ≥¥ Ï†ÑÏ≤¥ ÏÇ≠Ï†ú ÏöîÏ≤≠ Ìï®Ïàò
    // delete Î∞©Ïãù ÌÜµÏã† ÏöîÏ≤≠ -> @DeleteMapping("/api/dept/all")
    deleteAll() {
        return http.delete("/dept/all")
    }

    // // Î∂ÄÏÑúÎ™Ö Í≤ÄÏÉâ ÏöîÏ≤≠ Ìï®Ïàò 
    // // ÏøºÎ¶¨Ïä§Ìä∏ÎßÅ Î∞©Ïãù url : ?Î≥ÄÏàòÎ™Ö=Í∞í&Î≥ÄÏàòÎ™Ö2=Í∞í2....
    // // get Î∞©Ïãù ÌÜµÏã† ÏöîÏ≤≠ -> @GetMapping("/api/dept") , @RequestParam
    // findByDname(dname) {
    //     return http.get(`/dept?dname=${dname}`);
    // }

}

export default new DeptDataService();